<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    :visible="value"
    size="80vh"
    :destroy-on-close="true"
    direction="btt"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <!-- <el-scrollbar class="query-more-scroll-drawer">
    </el-scrollbar> -->
    <!-- 头部搜索 -->
    <el-row style="height: calc(100% - 50px);padding: 0 15px;box-sizing: border-box;">
      <table-com
        :ref="refName"
        class="table-com"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :show-operate="false"
        :refresh-key="refreshKey"
        :expand-list="expandList"
      />
    </el-row>

    <div class="drawer-footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'

export default {
  name: 'CustomEditCom',
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '自定义统计编辑'
    },
    formIdent: {
      type: String,
      default: ''
    },
    tableData: {
      type: Array,
      default: () => []
    },
    columnList: {
      type: Array,
      default: () => []
    },
    expandList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      refName: 'CustomEditComTable',
      refreshKey: new Date().getTime(),
      form: {},
      loading: false,
      rules: {},
      query: {}
    }
  },
  methods: {
    confirmDialog(formName) {
      this.$emit('input', false)
    },
    initSelect() {
      console.log('columnList: ', this.columnList)
    },
    beforeClose(done) {
      console.log('执行了')
      this.tableData = new Array(0)
      // this.form.wmsOrderMaterialJoinList = new Array(0)
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep .table-com {
    height: 100% !important;
  }
  ::v-deep .query-more-scroll-drawer {
    height: calc(100% - 45px);
  }
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: 45px;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  .drawer-footer{
    text-align: right;
  }
  </style>

