<template>
  <chart-card :title="title">
    <template>
      <!-- <v-chart :ref="refName" :height="100" :options="options" theme="macarons" :autoresize="true" /> -->
      <div style="height: 100%">
        <div :id="refName" :ref="refName" v-loading="chartLoading" style="height: 230px;" />
      </div>
    </template>
  </chart-card>
</template>

<script>
import '@/plugins/echarts/echartsTheme.js'
import ChartCard from '@/views/analysis/ChartCard'
import { zoom } from '@/mixins/echarts'

export default {
  name: 'ChartTwoCom',
  components: {
    ChartCard
  },
  mixins: [zoom],
  props: {
    title: {
      type: String,
      default: '供应商价格趋势'
    },
    chartLoading: {
      type: Boolean,
      default: false
    },
    chartData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      refName: 'ChartTwo',
      options: {},
      timer: null
    }
  },
  watch: {
    chartData: function(value) {
      if (!Object.keys(value).length) return
      this.resize()
      this.init()
      this.resize()
    }
  },
  beforeDestroy() {
    // Tip 清除计时器
    this.timer && clearInterval(this.timer)
  },
  mounted() {
    this.screenWidth = window.screen.width || document.documentElement.clientWidth
    this.screenHeight = window.screen.height || document.documentElement.clientHeight
    if (this.screenWidth && (this.screenWidth === 1024 || this.screenWidth < 1024)) {
      this.zoom = 0.8
    }
  },
  methods: {
    init() {
      const legendData = ['数量', '价格']
      const series = [
        {
          name: '数量',
          type: 'bar',
          data: [...this.chartData.orderNum], // 订单数量
          barWidth: 20
        },
        {
          name: '价格',
          type: 'line',
          yAxisIndex: 1,
          data: [...this.chartData.price]
        }
      ]
      const xAxis = [...this.chartData.supplierName]
      this.getCompleteOptions(legendData, series, xAxis)
      // charts.hideLoading()
    },
    /*
    * @legendData
    * @series
    * @xAxis
    */
    getCompleteOptions(legendData, series, xAxis) {
      this.options = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: legendData,
          left: 0
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          },
          {
            type: 'value',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        xAxis: {
          type: 'category',
          axisLabel: {
            show: true,
            interval: 0, // 使x轴文字显示全
            formatter: (params) => {
              var newParamsName = ''
              var paramsNameNumber = params.length
              var provideNumber = 10 // 一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber)
              if (paramsNameNumber > provideNumber) {
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = ''
                  var start = p * provideNumber
                  var end = start + provideNumber
                  if (p === rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber)
                  } else {
                    tempStr = params.substring(start, end) + '\n'
                  }
                  newParamsName += tempStr
                }
              } else {
                newParamsName = params
              }
              return newParamsName
            },
            rotate: -30
            // margin: 25
          },
          data: xAxis
        },
        series,
        dataZoom: [
          {
            type: 'inside', // 类型：内置型数据区域缩放组件
            xAxisIndex: 0,
            startValue: 0,
            endValue: 3
          }
        ]
      }
      const chart = this.$refs[this.refName]
      this.chart = chart && this.$echarts.init(chart, 'macarons')
      if (!this.chart) return
      this.chart.setOption(this.options)
      this.chart.resize()
      this.timer = setInterval(() => {
        if (this.options.dataZoom[0].endValue === xAxis.length - 1) {
          this.options.dataZoom[0].startValue = 0
          this.options.dataZoom[0].endValue = 3
        } else {
          this.options.dataZoom[0].endValue = this.options.dataZoom[0].endValue + 1
          this.options.dataZoom[0].startValue = this.options.dataZoom[0].startValue + 1
        }
        this.chart.setOption(this.options)
        this.chart.resize()
      }, 2500)
    }
  }
}
</script>

