<template>
  <el-row>
    <el-row :gutter="20" style="height: 100%;">
      <!-- 在途物资统计分析 -->
      <el-col :span="12" style="height: 100%;">
        <chart-one
          ref="ChartOne"
          :key="viewKey"
          v-bind="$attrs"
          :chart-loading="loading"
          :chart-data="percentageChart1"
          style="height: 100%;"
          @init="initCharts1"
        />
      </el-col>
      <el-col :span="12" style="height: 100%;">
        <chart-two
          ref="ChartTwo"
          :key="viewKey"
          v-bind="$attrs"
          :chart-loading="loading1"
          :chart-data="percentageChart2"
          style="height: 100%;"
          @init="initCharts2"
        />
      </el-col>
    </el-row>
  </el-row>

</template>

<script>
import ChartOne from './ChartOne'
import ChartTwo from './ChartTwo'
import { getChart1Api, getChart2Api } from '@/apis/modules/analysis/supplier'

export default {
  name: 'ChartsCom',
  components: {
    ChartOne,
    ChartTwo
  },
  props: {
    collapseValue: {
      type: String,
      default: ''
    },
    query: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      viewKey: 1,
      loading: false,
      loading1: false,
      percentageChart1: {},
      percentageChart2: {}
    }
  },
  mounted() {

  },
  methods: {
    initCharts1(params) {
      this.loading = true
      return new Promise((resolve, reject) => {
        getChart1Api(params).then(res => {
          const { code, data } = res
          if (!code) {
            console.log('initCharts1====>', data)
            this.percentageChart1 = data
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initCharts2(params) {
      this.loading1 = true
      return new Promise((resolve, reject) => {
        getChart2Api(params).then(res => {
          const { code, data } = res
          if (!code) {
            console.log(data)
            this.percentageChart2 = data
          }
          this.loading1 = false
          resolve(res)
        }).catch(err => {
          this.loading1 = false
          reject(err)
        })
      })
    },
    reset() {
      this.viewKey++
      this.$refs.ChartOne.resize()
      this.$refs.ChartTwo.resize()
      this.$nextTick(() => {
        this.collapseValue === '1' && this.initCharts1(this.query)
        this.collapseValue === '1' && this.initCharts2(this.query)
      })
    },
    handleUploadImg() {

    }
  }
}
</script>

<style lang='scss' scoped>

</style>
